"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { fetchAPI } from '@/lib/api';
import { Add, Edit, Trash } from 'iconsax-react';

export default function AdminBlogs() {
    const [blogs, setBlogs] = useState<any[]>([]);

    useEffect(() => {
        fetchAPI('/blogs').then(setBlogs).catch(console.error);
    }, []);

    const handleDelete = async (id: string) => {
        if (!confirm('Are you sure you want to delete this blog?')) return;
        try {
            await fetchAPI(`/blogs/${id}`, { method: 'DELETE' });
            setBlogs(blogs.filter(b => b.id !== id));
        } catch (error) {
            console.error('Failed to delete', error);
        }
    };

    return (
        <div>
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-black">Manage Blogs</h1>
                <Link href="/admin/blogs/new" className="flex items-center gap-2 px-6 py-3 bg-dark text-white rounded-lg font-bold hover:bg-black transition-colors">
                    <Add size="20" />
                    Create New
                </Link>
            </div>

            <div className="bg-white border border-gray-200 rounded-2xl overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-gray-50 border-b border-gray-100">
                        <tr>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-500">Title</th>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-500">Category</th>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-500">Date</th>
                            <th className="px-6 py-4 font-bold text-sm uppercase text-gray-500 text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-100">
                        {blogs.map((blog) => (
                            <tr key={blog.id} className="hover:bg-gray-50 transition-colors">
                                <td className="px-6 py-4 font-bold">{blog.title}</td>
                                <td className="px-6 py-4 text-sm text-gray-600">{blog.category}</td>
                                <td className="px-6 py-4 text-sm text-gray-600">{new Date(blog.createdAt).toLocaleDateString()}</td>
                                <td className="px-6 py-4 text-right">
                                    <div className="flex items-center justify-end gap-2">
                                        <button className="p-2 text-gray-400 hover:text-primary transition-colors">
                                            <Edit size="18" />
                                        </button>
                                        <button onClick={() => handleDelete(blog.id)} className="p-2 text-gray-400 hover:text-red-500 transition-colors">
                                            <Trash size="18" />
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                        {blogs.length === 0 && (
                            <tr>
                                <td colSpan={4} className="px-6 py-12 text-center text-gray-500">
                                    No blogs found. Create one!
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
